.App {
  text-align: left;
}

.App-header {
  background-color: #white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: blue;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a:active {
  text-decoration: underline;
}

.App-link {
  color: #61dafb;
}

.mt20 {
  margin-top: 20px;
}

.lt20 {
  margin-left: 20px;
}
.lt50 {
  margin-left: 50px;
}
.usa-list{margin-bottom:1em;margin-top:1em;line-height:1.5;padding-left:3ch}.usa-list li:last-child,.usa-list:last-child{margin-bottom:0}.usa-list ol,.usa-list ul{margin-top:.25em}.usa-list li{margin-bottom:.25em;max-width:68ex}
